<template>
  <header><h1>Table</h1></header>
  <div class="table-wrapper">
    <div class="table-cover">
      <data-table :columns="columns" :records="records" @sort="handleSort" />
    </div>
  </div>
  <footer><p>Footer</p></footer>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { mapGetters } from "vuex";

import { DataTable } from "@/components";
import { APISortOptions } from "./mock/types";

export default defineComponent({
  name: "App",
  components: { DataTable },
  mounted() {
    this.$store.dispatch("fetchRecords");
  },
  computed: {
    ...mapGetters(["records", "columns"]),
  },
  methods: {
    handleSort(options: APISortOptions) {
      this.$store.dispatch("fetchRecords", { sort: options });
    },
  },
});
</script>

<style lang="scss">
@import "./assets/styles";

.table-wrapper {
  flex: 1;
  position: relative;

  > .table-cover {
    @include cover;

    overflow: auto;
    background: var(--surface);
  }
}
</style>
